
import { defineComponent } from 'vue'
export default defineComponent({
  name: '',
  props: {
    type: {
      type: String,
      default: ''
    }
  },
  components: {
  },
  setup () {
    const buttonType = {
      primary: '#0068ff',
      success: '#67c23a',
      danger: '#f56c6c'
    }
    return {
      buttonType
    }
  }
})
