
import { defineComponent, ref, reactive } from 'vue'
import StDropdown from '@/components/StDropdown.vue'
import StDropdownItem from '@/components/StDropdownItem.vue'
import { useRouter, useRoute } from 'vue-router'
import { getUserInfo } from '@/api/login'
import { useStore } from 'vuex'
export interface UserProps {
  avatar?: {
    url: string;
    _id: string;
  };
  column: string;
  description: string;
  email: string;
  nickName: string;
  _id: string;
}
export default defineComponent({
  name: 'GlobarHeader',
  props: {

  },
  components: {
    StDropdown,
    StDropdownItem
  },
  setup () {
    const router = useRouter()
    const route = useRoute()
    const store = useStore()
    const goHome = () => {
      router.push('/')
    }
    const commandEmit = (val: string) => {
      const arr: string[] = ['/create', '/mycolum', '/useredit', '/login']
      if (val === '4') {
        localStorage.removeItem('token')
      }
      router.push(arr[+val - 1])
    }
    const doSomething = (i:number) => {
      const path = !i ? '/login' : '/register'
      router.push(path)
    }
    const userInfo = ref()
    if (localStorage.getItem('token')) {
      getUserInfo().then(res => {
        userInfo.value = res.data
        store.commit('SVAVE_USER_INFO', res.data)
      }).catch((error) => {
        console.log(error)
      // router.push('/login')
      })
    }

    return {
      route,
      userInfo,
      commandEmit,
      doSomething,
      goHome

    }
  }
})
